import axios from "axios";
import { useContext, useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { LoginInfoContext } from "../Index";

export default function Header(props){
    //  

    const{logininfo , setLogin} =  useContext(LoginInfoContext)
    //  axios   
    
    useEffect(()=>{
        axios.get("/member/login/info/get.do")
            .then( (r)=>{console.log(r)
                    setLogin(r.data)
            } )
            .catch( (e) => {console.log(e)})


    } ,[])
    
    const logoutHandler = ()=>{
        axios.get("/member/logout/get.do").then( (r)=>{ if(r.data){
            alert(" ")
            window.location.href = "/member/login"
        } } ).catch( e=>{})

    }

    const nowlogin = <ul><li>{logininfo.memail}</li><li><Link to="/"></Link></li><li><button type="button" onClick={logoutHandler}></button></li><li><Link to="/member/login"> () </Link></li> 
    <li><Link to ="/board/write"> </Link></li>
    <li><Link to ="/board">  </Link></li>
    <li><Link to ="/chat"></Link></li></ul>
    const nownotlogin = <ul>
        <li><Link to="/">  </Link></li>
        <li><Link to="/member/signup">  </Link></li>
        <li><Link to="/member/login">  </Link></li>
        <li><Link to ="/board/write"> </Link></li>
        <li><Link to ="/board">  </Link></li>
        <li><Link to ="/chat"></Link></li>
    </ul>
    return(<>
    <div>
        { logininfo ? nowlogin : nownotlogin}
    </div>
    </>)
}